body{
    font-family: sans-serif;
}

.title 
{
    text-align: center;
    font-size: 25px;
    height: 60px;
    line-height: 60px;
    border-radius: 8px;
    box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.2), 0 3px 20px 0 rgba(0, 0, 0, 0.19);
}

.TVShow {
    margin-top: 20px;
    height: 150px;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    user-select: none;
}

.shows-img {
    width: 125px;
    height: 125px;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    margin-left: 12.5px;
    margin-right: 12.5px;
    float: left;
    border-radius: 10px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.19);
}

.show-name {
    height: 150px;
    font-size: 23px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.loader {
    border: 4px solid #f3f3f3;
    border-radius: 50%;
    border-top: 4px solid #3498db;
    width: 30px;
    height: 30px;
    animation: spin 2s linear infinite;
    margin-top: 20px;
    display: block;
    margin-left: auto;
    margin-right: auto;
}
  
@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.show-details-img {
    margin-top: 20px;
    width: 250px;
    height: 250px;
    border-radius: 20px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    margin-bottom: 20px;
}

.description {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 7px;
    padding: 10px;
    font-size: 18px;
}

.ripple {
    position: relative;
    overflow: hidden;
    transform: translate(0, 0);
}
  
.ripple:after {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    pointer-events: none;
    background-image: radial-gradient(circle, #aaaaaa 10%, transparent 10.01%);
    background-repeat: no-repeat;
    background-position: 50%;
    transform: scale(10, 10);
    opacity: 0;
    transition: transform .5s, opacity 1s;
}
  
.ripple:active:after {
    transform: scale(0, 0);
    opacity: .3;
    transition: 0s;
}
